@import url(/assets/css/header.css);
@import url(/assets/css/hero.css);
@import url(/assets/css/fonctionnement.css);
@import url(/assets/css/restaurants.css);
@import url(/assets/css/footer.css);
@import url(/assets/css/menu.css);

:root {
  /* Fonts */
  --font-title: 'Shrikhand';
  --font-default: 'Roboto';
  /* Fonts Sizes */
  --section-title-font-size: 24px;
  --card-title-font-size: 18px;

  /* Fonts Weight */
  --section-title-font-weight: 400;
  --card-title-font-weight: 700;
  /* Colors */
  --artefact-color: #9356DC;
  --artefact-color-2: #7E7E7E;
  --footer-color: #353535;
  --secondary-color: #99E2D0;
  --secondary-text-color: #008766;
  --background-color: #f6f6f6;
  --location-background-color: #EAEAEA;
  --text-color: #000000;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
  background-color: var(--background-color);
  text-decoration: none;
  list-style: none;
}

.section-title {
  font-family: var(--font-default);
  font-size: var(--section-title-font-size);
  font-weight: 700;
}

.cta {
  background: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
  color: white;
  font-size: 16px;
  font-weight: 500;
  font-family: var(--font-default);
  text-align: center;
  vertical-align: middle;
  padding: 13px 18.75px;
  height: 50px;
  border-radius: 25px;
  box-shadow: 0px 4px 10px 0px #00000040;
}

.like {
  position: relative;
  display: flex;
  width: 22px;
  height: 21px;
  align-items: flex-start;
  &__heart {
    display: inline-block;
    width: 22px;
    height: 21px;
    &.fa-solid.fa-heart {
      position: absolute;
      background-image: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      font-size: 21px;
    }
    &.fa-regular.fa-heart {
      position: absolute;
      width: 22px;
      font-size: 21px;
      &:hover {
        display: none;
      }
    }
}
}
